define(function(require) {
	'use strict';

	function CatalogUtil() {
		// This method is used to trim the version
		// Ex. if version is '01.00.0000' then it returns trimmed version '1.0'
		// Ex. if version is '01.00' then it returns trimmed version '1.0'
		// Ex. if version is '01.01.0001' then it returns trimmed version '1.1.1'
		var trimVersion = function(version) {
			var vrs = version.split('.');
			var v1 = vrs[0].replace(/\b0+/g, '');
			var v2 = vrs[1].replace(/\b0+/g, '');
			var v3 = (vrs[2] === undefined) ? null : vrs[2].replace(/\b0+/g, '');

			if (v2.trim() === '') {
				v2 = '0';
			}

			var result = v1;
			result = result.concat('.' + v2);

			if (v3 !== null && v3.trim() !== '') {
				result = result.concat('.' + v3);
			}
			return result;
		};

		// This method is used to unTrim the version
		// Ex. if version is '1.1.10' then it returns '01.01.0010'
		// Ex. if version is '1.1.100' then it returns '01.01.0100'
		var unTrimVersion = function(version) {
			var vrs = version.split('.');
			var result = '';

			for (var i = 0; i < vrs.length; i++) {
				var v = vrs[i];

				if (i === 0 || i === 1) {
					if (v.length === 1) {
						v = '0' + v;
					}
					result = (i === 0) ? result.concat(v) : result.concat('.' + v);

				} else {
					if (v.length === 1) {
						v = '000' + v;
					} else if (v.length === 2) {
						v = '00' + v;
					} else {
						v = '0' + v;
					}
					result = result.concat('.' + v);
				}
			}
			if (vrs.length === 2) {
				result = result.concat('.0000');
			}
			return result;
		};

		this.util = {
			trimVersion: trimVersion,
			unTrimVersion: unTrimVersion
		};
	}

	return new CatalogUtil().util;

});



