define(function(require) {
	'use strict';

	var $ = require('jquery');

	function AuthHeader() {
		var self = this;
		var baseUrl = window.location.protocol + '//' + window.location.hostname + ':' + window.location.port;

		var getJwtTokenHeader = function() {
			var jwtToken = fetchJwtToken();
			return 'Bearer ' + jwtToken;
		};

		var fetchJwtToken = function() {
			var jwt = null;

			$.ajax({
				type: 'GET',
				url: baseUrl + '/ic/home/token',
				dataType: 'json',
				async: false,
				success: function(response) {
					jwt = response.jwt;
				},
				error: function(err) {
					jwt = null;
				}
			});

			return jwt;
		};

		self.getAuthHeader = function(requestHeader) {
			var authHeader = {};
			if ($.isEmptyObject(requestHeader) === true) {
				authHeader = {
					'Authorization': getJwtTokenHeader()
				};
			} else {
				authHeader = {
					'Authorization': requestHeader
				};
			}
			return authHeader;
		};
	}

	return new AuthHeader();

});



