define(function(require) {
	'use strict';

	require('ojs/ojcore');

	var ko = require('knockout'),
		_ = require('underscore');

	function ModelConfigurator() {
		var self = this;

		self.configureHeaderModel = function(cm) {
			var hm = cm.headerModel;

			_.defaults(hm, {
				headerText: '',
				headerButtons: [],
			});

			hm.catalog = function() {
				return cm;
			};
		};

		self.configureFilterPanelModel = function(cm) {
			var fpm = cm.filterPanelModel;

			_.defaults(fpm, {
				sortMenuItemsMap: {},
				filterMenuItems: []
			});

			fpm.catalog = function() {
				return cm;
			};
		};

		// configure the list view model passed by the consumer
		self.configureListViewModel = function(cm) {
			var lvm = cm.listViewModel;

			_.defaults(lvm, {
				requestHeader: {},
				dataArrayName: 'items',
				useDummyResponse: false,

				selectedRows: ko.observableArray([]),
				currSelectedRow: ko.observable(),
				prevSelectedRow: ko.observable(),

				navToDetailsLink: { // default values to navToDetailsLink
					type: {}, // it should be 'URL' or 'CUSTOM'
					url: {}, // should be updated in navToDetailsActionListener method if type is 'URL'
					customHandler: {} // The callback method should be defined if type is 'CUSTOM'
				}
			});

			lvm.catalogDataModel = cm.catalogDataModel;

			lvm.catalog = function() {
				return cm;
			};

			lvm.getRowData = function(id) {
				return lvm.catalogDataModel.getRowData(id);
			};

			lvm.getActualData = function(id) {
				return lvm.catalogDataModel.getActualData(id);
			};

			lvm.catalogOptionChangeListener = function(event, ui) {
				if (ui.option === 'selection') { // row selection option
					lvm.prevSelectedRow = ui.previousValue;
					lvm.currSelectedRow = ui.value;

					// if( ui.previousValue.length !== 0) {
					//     $('[id="'+ui.previousValue+'"]').css('background-color','white');
					// }
					// $('.oj-listview-item.oj-selected[id="'+ui.value+'"]').css('background-color','#E5E5E5');

					lvm.prevSelectedRowData = lvm.getRowData(ui.previousValue);
					lvm.currSelectedRowData = lvm.getRowData(ui.value);

					if (typeof lvm.rowSelectionListener === 'function') { // call the row selection option if available in users model
						lvm.rowSelectionListener(lvm.currSelectedRowData, event);
					}
				}

			};

			lvm.navToDetailsEvent = function(data, event) {
				if (typeof lvm.navToDetailsActionListener === 'function') { // call the navigation listener method provided by consumer
					lvm.navToDetailsActionListener(data, event);

					if (lvm.navToDetailsLink.type === 'URL') {
						window.location.href = lvm.navToDetailsLink.url;

					} else { //if (lvm.navToDetailsLink.type === 'CUSTOM') {
						if (typeof lvm[lvm.navToDetailsLink.customHandler] === 'function') {
							lvm[lvm.navToDetailsLink.customHandler](data, event);
						}
					}
				}
			};
		};
	}

	return new ModelConfigurator();
});



