define(function(require) {
	'use strict';

	require('ojs/ojcollectiontabledatasource');
	require('ojs/ojpagingtabledatasource');

	var ko = require('knockout'),
		oj = require('ojs/ojcore');

	function CatalogFilterModel() {

		var FilterModel = function() {
			var self = this;

			self.filterMenuItems;
			self.selectedFilters = ko.observable({});
			self.filtersCount = ko.observable(0);
			self.filterDetails = ko.observableArray([]);
			self.inputKeys = ko.observable('');

			self.removeFilterFn;
			self.catalogTypeName = '';
			self.dataModel;

			self.setDataModel = function(dataModel) {
				self.dataModel = dataModel;
				self.filteredDataSource = undefined; // set undefined to filteredDataSource so that the filter will take the new data collection.
			};

			self.setRemoveFilterFn = function(fn) {
				self.removeFilterFn = fn;
			};

			self.setFilterMenuItems = function(filterMenuItems) {
				self.filterMenuItems = filterMenuItems;
			};

			self.setCatalogTypeName = function(catalogName) {
				self.catalogTypeName = catalogName;
			};

			self.filterByNameFunction = function(model, attr, value) {
				var flag = true;

				var inputKeys = self.inputKeys();
				var name = model.get('name');

				flag = (name.toLowerCase().indexOf(inputKeys.toLowerCase()) > -1);
				if (flag === false) {
					return flag;
				}

				return flag;
			};

			self.filterByFunction = function(model, attr, value) {
				var flag = true;

				// do the filter from theh menu selection.
				var keys = Object.keys(self.selectedFilters());

				for (var i = 0; i < self.getSelectedFiltersCount(); i++) {
					var fltrDetail = self.selectedFilters()[keys[i]];

					var matchKey = fltrDetail.matchKey;
					var matchVal = fltrDetail.matchValue;

					var data = model.get(matchKey);
					flag = flag && (data === matchVal);

					if (flag === false)
						return flag;
				}
				return flag;
			};

			self.searchByName = function() {
				self.filterData(true);
			};

			self.filterData = function() {
				var inputKeysExist = self.inputKeys().length > 0;

				//if (self.filteredDataSource === undefined) {
				if (!self.filteredDataSource) {
					self.collection = self.dataModel.data.clone();

					self.filteredCollection = self.collection.clone();
					self.filteredDataSource = new oj.PagingTableDataSource(new oj.CollectionTableDataSource(self.filteredCollection));
				}

				var ret = null;

				if (inputKeysExist === false) {
					if (self.getSelectedFiltersCount() === 0) {
						// This case exists when there is no filters
						// When clear filters action or when removing the last filter
						// in this case no need to filter using comparator.
						//ret = self.collection.toJSON();
						ret = self.collection.where({
							name: {
								value: null,
								comparator: self.filterByFunction
							}
						});

					} else {
						// if inputKeys are empty then use filterByFunction which filters the data for all the selected filters.
						ret = self.collection.where({
							name: {
								value: null,
								comparator: self.filterByFunction
							}
						});
					}

				} else { //if(inputKeysExist === true)
					// if inputKeys exist then first filter the whole data with the name criteria first
					ret = self.collection.where({
						name: {
							value: null,
							comparator: self.filterByNameFunction
						}
					});

					// if there are already selected filters then use the above filtered collection and use
					// filterByFunction comparator to filter with the existing filters.
					if (self.getSelectedFiltersCount() > 0) {
						self.collection1 = new oj.Collection(ret);
						ret = self.collection1.where({
							name: {
								value: null,
								comparator: self.filterByFunction
							}
						});
					}
				}

				self.filteredCollection.reset(ret);

				self.assignToDatasource(self.filteredCollection);

				self.setFilterDetails();
			};

			self.handleFilterMenuSelection = function(event, ui) {
				var selectedFilterKey = event.originalEvent.currentTarget.id;
				var selectedKey = selectedFilterKey.split('-');
				var fltrMenuItem = self.getFilterMenuItemDetail(selectedFilterKey);

				self.selectedFilters()[selectedKey[0]] = fltrMenuItem;
				self.setFilterDetails();
				self.filterData();
			};

			self.getFilterMenuItemDetail = function(key) {
				var fltrMenus = self.filterMenuItems;

				for (var i = 0; i < fltrMenus.length; i++) {
					var fltrMenu = fltrMenus[i].filterMenuItems;
					for (var j = 0; j < fltrMenu.length; j++) {
						var fltrMenuItem = fltrMenu[j];
						if (fltrMenuItem.key === key) {
							return fltrMenuItem;
						}
					}
				}
			};

			self.clearFilters = function() {
				var keys = Object.keys(self.selectedFilters());

				for (var i = 0; i < keys.length; i++) {
					delete self.selectedFilters()[keys[i]];
				}

				self.inputKeys('');
				self.setFilterDetails();
				self.filterData();

			};

			self.removeFilter = function(filterKey) {
				var keys = Object.keys(self.selectedFilters());

				for (var i = 0; i < keys.length; i++) {
					if (filterKey === keys[i]) {
						delete self.selectedFilters()[keys[i]];
						break;
					}
				}

				if (filterKey === 'input_key') {
					self.inputKeys('');
				}

				self.setFilterDetails();
				self.filterData();

			};

			self.getSelectedFiltersCount = function() {
				var keys = Object.keys(self.selectedFilters());
				return keys.length;
			};

			self.assignToDatasource = function(dataCollection) {
				self.dataModel.jsonDataSource = new oj.PagingTableDataSource(new oj.CollectionTableDataSource(dataCollection));
				self.dataModel.dataSource(self.dataModel.jsonDataSource);
			};

			self.setFilterDetails = function() {
				self.filterDetails([]);

				var inputKeys = self.inputKeys();
				if (inputKeys.length > 0) {
					self.filterDetails.push({
						matchKey: 'input_key',
						matchValue: inputKeys,
						filterKey: 'input_key',
						filterText: self.catalogTypeName.charAt(0).toUpperCase() + self.catalogTypeName.slice(1) + ' Name contains ',
						filterLabel: inputKeys,
						removeFltrfn: self.removeFilterFn
					});
				}

				for (var i = 0; i < self.getSelectedFiltersCount(); i++) {
					var key = Object.keys(self.selectedFilters())[i];
					var val = self.selectedFilters()[key];
					self.filterDetails.push({
						matchKey: val.matchKey,
						matchValue: val.matchValue,
						filterKey: key,
						filterText: self.catalogTypeName.charAt(0).toUpperCase() + self.catalogTypeName.slice(1) + ' ' + key.charAt(0).toUpperCase() + key.slice(1) + ' is ',
						filterLabel: val.label.toUpperCase(),
						removeFltrfn: self.removeFilterFn
					});
				}

				var fltrsCount = self.getSelectedFiltersCount();
				if (self.inputKeys().length > 0) {
					fltrsCount++;
				}

				self.filtersCount(fltrsCount);
			};
		};

		var getInstance = function(dataModel, filterMenuItems) {
			var filterModel = new FilterModel();
			filterModel.setDataModel(dataModel);
			filterModel.setFilterMenuItems(filterMenuItems);
			return filterModel;
		};

		this.catalogFilterModel = {
			getInstance: function(dataModel, filterMenuItems) {
				return getInstance(dataModel, filterMenuItems);
			}
		};
	}

	return new CatalogFilterModel().catalogFilterModel;
});



