define(function(require) {
	'use strict';

	require('ojs/ojcollectiontabledatasource');
	require('ojs/ojjsontreedatasource');
	require('ojs/ojbutton');
	require('ojs/ojcore');

	var ko = require('knockout'),
		$ = require('jquery');

	function CatalogSortingModel() {

		var SortingModel = function() {
			var self = this;

			self.sortMenuItemsMap = null;
			self.currentSortKey = null;

			self.sortMenuItems = [];

			self.dataModel = null;

			self.setDataModel = function(dataModel) {
				self.dataModel = dataModel;
			};

			self.setSortMenuItemsMap = function(menuItemsMap) {
				self.sortMenuItemsMap = menuItemsMap;
				if (Object.keys(self.sortMenuItemsMap).length === 0) {
					return;
				} else {
					self.initSortingMenuItems();
					self.currentSortKey = self.getDefaultMenuSortKey();

					self.sortMenuButtonLabel = ko.observable(self.getDefaultMenuItemLabel());
					self.sortMenuIconSrc = ko.observable(self.getDefaultMenuItemIcon());
					self.sortMenuTitle = ko.observable(self.getDefaultMenuItemTitle());
				}
			};

			self.initSortingMenuItems = function() {
				for (var i = 0; i < Object.keys(self.sortMenuItemsMap).length; i++) {
					var id = (Object.keys(self.sortMenuItemsMap))[i];
					var menuItemDetail = self.sortMenuItemsMap[id];

					self.sortMenuItems.push({
						'id': id,
						'key': menuItemDetail.key,
						'direction': menuItemDetail.direction,
						'label': menuItemDetail.label,
						'icon': menuItemDetail.icon,
						'title': menuItemDetail.icon
					});
				}
			};

			self.getDefaultMenuSortKey = function() {
				var _smiMap = getSoftMenuItemsMap();
				return (Object.keys(_smiMap))[0];
			};

			var getSoftMenuItemsMap = function() {
				return self.sortMenuItemsMap;
			};

			self.getDefaultMenuItemLabel = function() {
				var _smiMap = getSoftMenuItemsMap();

				var key = (Object.keys(_smiMap))[0];
				var menuItemValue = _smiMap[key];

				return menuItemValue.label;
			};

			self.getDefaultMenuItemIcon = function() {
				var _smiMap = getSoftMenuItemsMap();

				var key = (Object.keys(_smiMap))[0];
				var menuItemValue = _smiMap[key];

				return 'icon-sort-' + menuItemValue.direction;
			};

			self.getDefaultMenuItemTitle = function() {
				var _smiMap = getSoftMenuItemsMap();

				var key = (Object.keys(_smiMap))[0];
				var menuItemValue = _smiMap[key];

				return menuItemValue.title;
			};

			self.handleSortCriteriaChange = function(event, ui) {
				var selectedMenuId = event.originalEvent.currentTarget.id;

				self.sortData(selectedMenuId);
				self.setCurrentSortingKey(selectedMenuId);
			};

			self.sortData = function(sortingKey) {
				if (Object.keys(self.sortMenuItemsMap).length === 0) {
					return;
				} else {
					var sortMenuItem = self.sortMenuItemsMap[sortingKey];

					self.setCurrentSortingKey(sortingKey);

					self.collection = self.dataModel.getDataSourceCollection();
					self.collection.sort(sortMenuItem);
					self.dataModel.setDataSourceCollection(self.collection);

					self.sortMenuButtonLabel(sortMenuItem.label);
					self.sortMenuIconSrc('icon-sort-' + sortMenuItem.direction);
					self.sortMenuTitle(sortMenuItem.title);

					$('#menuButton').ojButton('option', 'icons.start', sortMenuItem.icon);
				}
			};

			self.setCurrentSortingKey = function(sortingKey) {
				self.currentSortKey = sortingKey;
			};

			self.getCurrentSortingKey = function() {
				return self.currentSortKey;
			};

		};

		var getInstance = function(dataModel, menuItemsMap) {
			var sortingModel = new SortingModel();
			sortingModel.setDataModel(dataModel);
			sortingModel.setSortMenuItemsMap(menuItemsMap);
			return sortingModel;
		};

		this.sortingModel = {
			getInstance: function(dataModel, menuItemsMap) {
				return getInstance(dataModel, menuItemsMap);
			}
		};
	}

	return new CatalogSortingModel().sortingModel;

});



