define(function(require) {
	'use strict';

	var CatalogModel = require('Catalog'),
		CatalogCss = require('css/metadata-list/catalog.css'),
		infoPopupContentTemplate = require('metadata-list-info-popup-content-template'),
		catalogTemplate = require('metadata-list-template');

	function MetadataList() {
		CatalogCss.use();

		this.app = CatalogModel;
		this.infoPopupContentTemplate = infoPopupContentTemplate;
		this.catalogTemplate = catalogTemplate;
	}

	return new MetadataList();
});



