define(function(require) {
	'use strict';

	require('ojs/ojcore');
	require('ojs/ojpopup');

	var ko = require('knockout'),
		$ = require('jquery');

	function PopupGenerator() {

		// Popup object
		var Popup = function(popupData) {
			var self = this;

			var _popupContentTemplate = require('metadata-list-info-popup-content-template');
			var _popupData = popupData;
			var _popup;

			self.setPopupData = function(popupData) {
				_popupData = popupData;
			};

			self.setPopupTemplateContent = function(templateContent) {
				_popupContentTemplate = templateContent;
			};

			self.launch = function(launcherElementId) {
				$('#catalogPopupId').ojPopup('close'); // close if already opened

				$(_popupContentTemplate).appendTo($('#catalogPopupContentId'));
				_popup = $('#catalogPopupId');

				ko.cleanNode(_popup[0]);
				ko.applyBindings(_popupData, _popup[0]);

				$(_popup).on('ojclose', function(event, ui) {
					$('#catalogPopupContentId').children().remove();
				});

				setTimeout(function() {
					$(_popup).ojPopup('open', $('[id="' + launcherElementId + '"]')[0]);
				}, 100);

			};

		};

		this.generator = {
			createPopup: function(data) {
				return new Popup(data);
			}
		};
	}

	return new PopupGenerator().generator;
});



