define(function(require) {
	'use strict';

	require('ojs/ojjsontreedatasource');
	require('ojs/ojcollectiontabledatasource');
	require('ojs/ojpagingtabledatasource');

	const ko = require('knockout'),
		oj = require('ojs/ojcore'),
		$ = require('jquery');

	var ServerCommunicationHandler = require('MetadataListCommunicationHandler');

	function CatalogDataModel() {

		var DataModel = function(_catalogModel) {
			var self = this;

			self.dataArrayName = null; // array variable name
			self.requestHeader = {}; // request header extra key value pairs can be set by consumer
			self.response = null; // actual date from rest call
			self.data = null; // unique data generated by consumers callback method
			self.jsonDataSource = null; // table or paing table datasource object created out of self.data
			self.dataSource = ko.observable(); // observable datasource created out of jsonDataSource.
			self.catalogModel = _catalogModel;

			self.getGenericData = function(jsonData, fn, dataArrayName) {
				//if( dataArrayName === null ) {
				if (!dataArrayName) {
					jsonData['items'] = jsonData;
					dataArrayName = 'items';
				}
				var _arrayName = dataArrayName; // || 'items';
				self.dataList = [];

				if ((Object.keys(jsonData).length === 0)) {
					jsonData[_arrayName] = [];
				}

				if (jsonData[_arrayName]) {
					for (var i = 0; i < jsonData[_arrayName].length; i++) {
						var rdata = fn(jsonData[_arrayName][i]);
						var rowJsonObject = rdata.getJsonObject(self.catalogModel);
						self.dataList.push(rowJsonObject);
					}
				}

				return new oj.Collection(self.dataList);
			};

			self.getRowData = function(id) {
				var rowData = self.data.get(id);
				//if( rowData !== null && rowData !== undefined ) {
				if (rowData) {
					return $.extend(true, {}, rowData.attributes);
				}
				return {};
			};

			self.getActualData = function(id) {
				var response1 = {};
				var _arrayName = self.dataArrayName || 'items';

				//if( self.dataArrayName === null ) {
				if (!self.dataArrayName) {
					response1['items'] = self.response;
					_arrayName = 'items';
				} else {
					response1 = self.response;
				}

				var tempCollection = new oj.Collection(response1[_arrayName]);
				var actualRowData = tempCollection.get(id);

				var _actualRowData = $.extend(true, {}, actualRowData.attributes);

				return _actualRowData;
			};

			self.initData = function(restUrl, fn, dataArrayName, requestHeader) {
				self.dataArrayName = dataArrayName;
				self.requestHeader = requestHeader;

				self.response = ServerCommunicationHandler.fetchData(restUrl, requestHeader);
				self.data = self.getGenericData(self.response, fn, dataArrayName);

				self.jsonDataSource = new oj.PagingTableDataSource(new oj.CollectionTableDataSource(self.data));
				self.dataSource(self.jsonDataSource);
			};

			self.getDataSourceCollection = function() {
				return self.dataSource();
			};

			self.setDataSourceCollection = function(collection) {
				self.dataSource(collection);
			};
		};

		var getInstance = function(catalogModel) {
			return new DataModel(catalogModel);
		};

		this.dataModel = {
			getInstance: function(catalogModel) {
				return getInstance(catalogModel);
			}
		};
	}

	return new CatalogDataModel().dataModel;
});



