define(function(require) {
	'use strict';

	require('ojs/ojcore');
	require('ojs/ojpopup');
	require('ojs/ojdialog');

	var ko = require('knockout'),
		$ = require('jquery'),
		_ = require('underscore');

	function DialogGenerator() {

		// Popup object
		var Dialog = function(dialogData) {
			var self = this;

			var _dialogHeaderTemplate = require('metadata-dialog-header-template');
			var _dialogContentTemplate = require('metadata-dialog-default-template');
			var _dialogData = dialogData || {};
			var _dialog;

			self.setDialogData = function(dialogData) {
				_dialogData = dialogData;
			};

			self.setDialogTemplateContent = function(templateContent) {
				_dialogContentTemplate = templateContent;
			};

			self.launch = function() {
				_.defaults(_dialogData, {
					title: 'Catalog',
					dialogHelpTopicId: 'catalogHelpTopicId',
					close: function() {
						$(_dialog).ojDialog('close');
					}
				});

				$(_dialogHeaderTemplate).appendTo($('#catalogDialogHeaderId'));
				$(_dialogContentTemplate).appendTo($('#catalogDialogContentId'));

				_dialog = $('#catalogDialogId');

				ko.cleanNode(_dialog[0]);
				ko.applyBindings(_dialogData, _dialog[0]);

				$(_dialog).on('ojclose', function(event, ui) {
					$('#catalogDialogHeaderId').children().remove();
					$('#catalogDialogContentId').children().remove();
					$('.oj-dialog-header.oj-helper-clearfix').remove();
				});

				$(_dialog).ojDialog('open');
			};

			self.close = function() {
				$(_dialog).ojDialog('close');
			};

		};

		this.generator = {
			createDialog: function(data) {
				return new Dialog(data);
			}
		};
	}

	return new DialogGenerator().generator;
});



