define(function(require) {
	'use strict';

	require('ojs/ojpagingcontrol');
	require('ojs/ojinputtext');
	require('ojs/ojconveyorbelt');
	require('ojs/ojprogressbar');
	require('ojs/ojgauge');
	require('ojs/ojrouter');
	require('ojs/ojknockout');
	require('ojs/ojarraytabledatasource');
	require('ojs/ojpagingtabledatasource');
	require('ojs/ojoffcanvas');
	require('ojs/ojbutton');
	require('ojs/ojmenu');
	require('ojs/ojlistview');
	require('ojs/ojcore');
	require('knockout');

	var $ = require('jquery');

	var ModelConfigurator = require('ModelConfigurator'),
		CatalogFilterModel = require('CatalogFilterModel'),
		CatalogSortingModel = require('CatalogSortingModel'),
		CatalogDataModel = require('CatalogDataModel'),
		util = require('CatalogUtil'),
		rowData = require('CatalogRowData'),
		popupGenerator = require('PopupGenerator'),
		dialogGenerator = require('DialogGenerator'),
		serverCommunicationHandler = require('MetadataListCommunicationHandler'),
		catalogTemplate = require('metadata-list-template'),
		infoPopupContentTemplate = require('metadata-list-info-popup-content-template'),
		defaultDialogContentTemplate = require('metadata-dialog-default-template');

	function catalogContentViewModel() {

		var CatalogModel = function() {
			var self = this;
			self.timer = setTimeout(function() {}, 0);

			self.currentTime = (new Date()).toUTCString();
			self.catalogTypeName = '';

			self.catalogDataModel = CatalogDataModel.getInstance(self);

			self.fetchDummyData = function() {
				try {
					var originalAjax = $.ajax;

					$.ajax = function(model) {
						model.success(self.listViewModel.response);
					};

					self.catalogDataModel.initData(self.listViewModel.restURL,
						self.listViewModel.constructRowData,
						self.listViewModel.dataArrayName,
						self.listViewModel.requestHeader);
				} finally {
					$.ajax = originalAjax;
				}
			};

			self.fetchData = function() {
				if (self.listViewModel.useDummyResponse === true) {
					self.fetchDummyData();
				} else {
					self.catalogDataModel.initData(self.listViewModel.restURL,
						self.listViewModel.constructRowData,
						self.listViewModel.dataArrayName,
						self.listViewModel.requestHeader);
				}
			};

			self.initDataModel = function() {
				self.fetchData();
			};

			self.initFilterModel = function() {
				self.filterModel = CatalogFilterModel.getInstance(self.catalogDataModel, self.filterPanelModel.filterMenuItems);
				self.filterModel.setCatalogTypeName(self.catalogTypeName);
				self.filterModel.setRemoveFilterFn(self.removeFilter);
			};

			self.initSortingModel = function() {
				self.sortingModel = CatalogSortingModel.getInstance(self.catalogDataModel, self.filterPanelModel.sortMenuItemsMap);
			};

			// common event handler for sort menu item
			self.handleSortCriteriaChange = function(event, ui) {
				self.sortingModel.handleSortCriteriaChange(event, ui);
			};

			// common event handler for filter menu item
			self.handleFilterMenuSelection = function(event, ui) {
				self.filterModel.handleFilterMenuSelection(event, ui);
			};

			// common event handler for search by name
			self.searchByName = function(event) {
				clearTimeout(self.timer);

				self.timer = setTimeout(function() {
					self.timer = null;
					self.filterModel.searchByName();
				}, 500);
			};

			// common event handler for refresh action
			self.refreshHandler = function() {
				self.refresh();
			};

			self.refresh = function() {
				self.fetchData();

				self.filterModel.setDataModel(self.catalogDataModel);
				self.sortingModel.setDataModel(self.catalogDataModel);

				self.filterModel.filterData();
				self.sortingModel.sortData(self.sortingModel.getCurrentSortingKey());
			};

			// clear filters event
			self.clearFilters = function() {
				self.filterModel.clearFilters();
			};

			// remove filter event
			self.removeFilter = function(data) {
				self.filterModel.removeFilter(data.filterKey);
			};

			// Navigation to Details Event
			self.navToDetailsEvent = function(data, event) {
				self.listViewModel.navToDetailsEvent(data, event);
			};

			self.copyUrlActionEvent = function(data, event) {
				self.copyToClipboard(data.endpointUrl);
			};

			self.copyToClipboard = function(dataToCopy) {
				var $temp = $('<input>');
				$('body').append($temp);
				var s = $temp.val(dataToCopy).select();
				document.execCommand('copy');
				$temp.remove();
				return s[0].value;
			};

			self.launchActionsMenu = function(data, event) {
				$('#catalogPopupId').ojPopup('close'); // close the info popup if it is opened

				var menuId = 'actionMenuItems_' + data.id;
				setTimeout(function() {
					$('[id="' + menuId + '"]').ojMenu('open', event);
				}, 50);
			};

			// Action handler for action menu item
			self.actionMenuItemSelectionEvent = function(event, ui) {
				var selectedMenuId = ui.item[0].id;
				var menu_id = selectedMenuId.split('#')[0];
				var obj_id = selectedMenuId.split('#')[1];
				var rowData = self.listViewModel.getRowData(obj_id);

				var menu_json = self.listViewModel['getActionMenuDetails'](rowData);

				var menuItems = menu_json.menuItems;

				for (var i = 0; i < menuItems.length; i++) {
					if (menuItems[i].menu_id === menu_id) {
						var menuItemDetail = menuItems[i];
						self.listViewModel[menuItemDetail.action_handler](self.listViewModel.getRowData(obj_id), event);
					}
				}
			};

			self.launchInfoPopupEvent = function(data, event) {
				if (typeof self.listViewModel.launchInfoPopupEventListener === 'function') {
					self.listViewModel.launchInfoPopupEventListener(data, event);
				}
			};

			self.statusActionEvent = function(data, event) {
				if (typeof self.listViewModel.statusActionEventListener === 'function') {
					self.listViewModel.statusActionEventListener(data, event);
				}
			};

			self.getRowData = function(rowId) {
				return self.listViewModel.getRowData(rowId);
			};

			self.getActualRowData = function(rowId) {
				return self.listViewModel.getActualData(rowId);
			};

			self.util = function() {
				return util;
			};

			self.rowData = function() {
				return rowData.createInstance();
			};

			self.popupGenerator = function() {
				return popupGenerator;
			};

			self.dialogGenerator = function() {
				return dialogGenerator;
			};

			self.serverCommunicationHandler = function() {
				return serverCommunicationHandler;
			};

			self.catalogTemplate = function() {
				return catalogTemplate;
			};

			self.infoPopupContentTemplate = function() {
				return infoPopupContentTemplate;
			};

			self.defaultDialogContentTemplate = function() {
				return defaultDialogContentTemplate;
			};

			self.mouseout = function(data, event) {
				data.showActions(false);
			};

			self.mouseover = function(data, event) {
				data.showActions(true);
			};

			self.closeCatalogDialog = function(data, event) {
				$('#catalogDialogId').ojDialog('close');
			};

		};

		var catalogModel;

		var createInstance = function(_headerModel, _filterPanelModel, _listViewModel, _metadataTypeName) {
			catalogModel = new CatalogModel();

			catalogModel.catalogTypeName = _metadataTypeName;
			catalogModel.headerModel = _headerModel;
			catalogModel.filterPanelModel = _filterPanelModel;
			catalogModel.listViewModel = _listViewModel;

			ModelConfigurator.configureListViewModel(catalogModel);
			ModelConfigurator.configureHeaderModel(catalogModel);
			ModelConfigurator.configureFilterPanelModel(catalogModel);

			catalogModel.initDataModel();
			catalogModel.initSortingModel();
			catalogModel.initFilterModel();

			catalogModel.filterModel.filterData();
			catalogModel.sortingModel.sortData(catalogModel.sortingModel.getCurrentSortingKey());

			return catalogModel;
		};

		this.catalogContentModel = {
			createInstance: createInstance
		};
	}

	return new catalogContentViewModel().catalogContentModel;

});



