define(function(require) {
	'use strict';

	require('ojs/ojcore');
	require('promise');

	var ko = require('knockout');
	const $ = require('jquery');

	function CatalogRowData() {
		var props = ['id',
			'code',
			'name',
			'version',
			'description',
			'type',
			'projectType',
			'mode_type',
			'modified',
			'status',
			'pattern',
			'film_strip',
			'info_icon',
			'usage',
			'film_strip_data'
		];

		var CatalogRowDataObject = function() {
			var rowObj = this;

			rowObj.getJsonObject = function(catalogModel) {
				var jsonRow = {};

				jsonRow = $.extend(true, {}, rowObj);
				delete jsonRow.getJsonObject;

				for (let i in props) {
					var prop = props[i];
					jsonRow[prop] = rowObj[prop];
				}

				// set defaults if the property is undefined
				jsonRow['info_icon'] = jsonRow['info_icon'] != undefined ? jsonRow['info_icon'] : true;

				// set the menu items for the row
				jsonRow.menuItems = [];

				if (catalogModel.listViewModel.getActionMenuDetails) {
					jsonRow.menuItems = catalogModel.listViewModel.getActionMenuDetails(this).menuItems;
					for (let i = 0; i < jsonRow.menuItems.length; i++) {
						if (!jsonRow.menuItems[i].disabled)
							jsonRow.menuItems[i].disabled = false;

					}
				}

				jsonRow.showActions = ko.observable(false);
				jsonRow.activeActions = ko.observable(false);

				if (typeof jsonRow.fetchRuntimeData === 'function') {
					jsonRow.runtimeData = {
						'loading': ko.observable(false),
						'successRate': ko.observable(0),
						'success': ko.observable(0),
						'failure': ko.observable(0)
					};
					jsonRow.loadRuntimeData = function() {
						jsonRow.runtimeData.loading(true);
						const runtimeDataPromise = jsonRow.fetchRuntimeData();

						runtimeDataPromise.then(function(data) {
							jsonRow.runtimeData.successRate(data.successRate);
							jsonRow.runtimeData.success(data.success);
							jsonRow.runtimeData.failure(data.failure);
							jsonRow.runtimeData.loading(false);
						});
					};
					jsonRow.loadRuntimeData();
				}

				return jsonRow;
			};
		};

		this.createInstance = function() {
			return new CatalogRowDataObject();
		};
	}

	return new CatalogRowData();

});



