define(function(require) {
	'use strict';

	var $ = require('jquery');
	var authHeader = require('AuthHeader');

	function MetadataListCommunicationHandler() {
		var self = this;
		var baseUrl = window.location.protocol + '//' + window.location.hostname + ':' + window.location.port;

		self.fetchData = function(url, requestHeader) {
			var restUrl = baseUrl + url;
			self.restResponse = {};

			$.ajax({
				type: 'GET',
				url: restUrl,
				dataType: 'json',
				async: false,
				headers: authHeader.getAuthHeader(requestHeader),
				success: function(res) {
					self.restResponse = res;
				},
				error: function(err) {
					self.restResponse = {};
				}
			});
			return self.restResponse;
		};
	}

	return new MetadataListCommunicationHandler();

});



